<template>
  <header class="header">
    <div class="clearfix">
      <router-link class="fl_left" to="/">
        <img class="logo" src="../assets/image/logo@2x.png" width="25">
        <span class="text">TJUI</span>
      </router-link>
      <div class="fl_right">
        <img src="https://website.didiglobal.com/dist/media/menu.3f7d9019.svg" width="25" @click="handleToggle">
        <!-- <img src="https://website.didiglobal.com/dist/media/close1.027a5639.svg" alt=""> -->
      </div>
    </div>
  </header>
</template>

<script>
import Logo from "./logo";
export default {
  methods: {
    handleToggle() {
      const $ele = document.querySelector("#sidebar"),
        className = $ele.getAttribute("class");

      if (className === "sidebar-wrap") {
        $ele.classList.add("open");
      } else {
        $ele.classList.remove("open");
      }
    }
  },
  components: {
    "tj-logo": Logo
  }
};
</script>

<style lang="scss" scoped>
.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 98;
  height: 45px;
  line-height: 45px;
  background: #fff;
  .clearfix {
    padding: 0 15px;
  }
  .fl_left {
    .logo {
      position: relative;
      top: 5px;
    }
    .text {
      color: #000;
      margin-left: 3px;
      // font-weight: bolder;
      font-size: 22px;
      margin-left: -1px;
    }
  }
  .fl_right {
    img {
      position: relative;
      top: 2px;
    }
  }
}
</style>



